import React, { Component } from "react";
import {
  CCardGroup,
  CCard,
  CCardBody,
  CCardHeader,
  CImg,
  CRow,
  CCol,
  CFormGroup,
  CLabel,
  CSelect,
  CButton,
  CDataTable,
  CInput,
} from "@coreui/react";
import { CChartPie, CChartLine, CChartBar } from "@coreui/react-chartjs";
import CIcon from "@coreui/icons-react";
import DatetimeRangePicker from "react-datetime-range-picker";
import moment from "moment";

export default class Dashboard extends Component {
  constructor() {
    super();
    this.state = {
      dashboardList: [],
      diagnosaDataSets: [],
      genderDataSets: [],
      usiaDataSets: [],
      statusMonitoringDataSets: [],
      jenisPenggunaDataSets: [],
      lokasiPasien: [],
      namaPuskesmas: "",
      aktivuser: "",
      totalPengguna: 0,
      pasienProkes: 0,
      pasienSelesaiDimonitor: 0,
      jumlahVolunteer: 0,
      startDate: "",
      endDate: "",
      //PIE CHART
      // filterDiagnosa: [],
      // filterJenisKelamin: [],
      // filterUsia: [],
      // filterStatusMonitoring: [],
      // filterJenisPengguna: [],
    };
  }
  componentDidMount() {
    this.dasboardList()
  }

  handleInputChange = (event) => {
    var k = event.target.id;
    this.setState({
      [k]: event.target.value,
    });
  };
  handleDateChange = (event) => {
    this.setState({
      startDate: moment(event.start).format("YYYY-MM-DDT00:00:00.000Z"),
      endDate: moment(event.end).format("YYYY-MM-DDT23:59:59.000Z"),
    });
  };
  handlePuskesmasChange = (event) => {
    var k = event.target.id;
    this.setState({
      [k]: event.target.value,
    });
    if (event.target.value == "") {
      this.getAllPatient(event.target.value, this.state.aktivuser);
    } else {
      this.getAllPatient(event.target.value, "");
    }
    this.getListUser(event.target.value);
  };
  handleAktivChange = (event) => {
    var k = event.target.id;
    this.setState({
      [k]: event.target.value,
    });
    this.getAllPatient(this.state.namaPuskesmas, event.target.value);
  };
  dasboardList = () => {
    fetch("https://api.aktiv.co.id/api/aktivengine/v2/dashboard/get", {
      method: "GET",
      headers: new Headers({
        "Content-Type": "application/json",
        Authorization: localStorage.getItem("token"),
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        var usiaDataSets = [
          {
            backgroundColor: ["#3699FF", "#F6BF29", "#E54F5B", "#66B983"],
            data: data.data.filterUsia
          },
        ];
        var statusMonitoringDataSets = [
          {
            backgroundColor: ["#3699FF", "#F6BF29", "#E54F5B", "#E5E5E5", "#C7B9FF"],
            data: data.data.filterStatusMonitoring
          },
        ];
        var genderDataSets = [
          {
            backgroundColor: ["#3699FF", "#E46651"],
            data: data.data.filterJenisKelamin
          },
        ];
        var diagnosaDataSets = [
          {
            backgroundColor: ["#3699FF", "#E54F5B", "#F6BF29", "#E5E5E5", "#025B95"],
            data: data.data.filterDiagnosa
          },
        ];
        var jenisPenggunaDataSets = [
          {
            backgroundColor: ["#3699FF", "#F6BF29"],
            data: data.data.filterJenisPengguna
          },
        ];
        var lokasiPasien = []
        const templokasiPasien = Object.entries(data.data.filterLokasiPasien);
        templokasiPasien.forEach(([key, value]) => {
          if (key != "") {
            lokasiPasien.push({
              kelurahan: key,
              counting: value,
            });
          }
        });
        this.setState({
          dashboardList: data.data,
          usiaDataSets: usiaDataSets,
          statusMonitoringDataSets: statusMonitoringDataSets,
          genderDataSets: genderDataSets,
          diagnosaDataSets: diagnosaDataSets,
          jenisPenggunaDataSets: jenisPenggunaDataSets,
          lokasiPasien: lokasiPasien
        });
      })
      .catch((error) => console.log(error));
  };
  downloadExcel = () => {
    var url;
    if (localStorage.getItem("role") == "admin") {
      url = `https://report.aktiv.co.id/api/report-dashboard`;
    } else {
      url = `https://report.aktiv.co.id/api/report-dashboard/${localStorage.getItem(
        "puskesmasId"
      )}`;
    }
    fetch(url, {
      method: "GET",
      headers: new Headers({
        "Content-Type": "application/vnd.ms-excel;charset=UTF-8",
        responseType: "blob",
        Authorization: `${localStorage.getItem("token")}`,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        console.log("Get Download Response =>", data);

        var link = document.createElement("a");
        link.download = "Reportnya";
        link.href = data.fileUrl;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  render() {
    return (
      <>
        <CRow>
          <CCol sm="12">
            <CFormGroup>
              <CButton
                className="btn-outline-success float-right my-2 mr-3"
                onClick={() => {
                  this.downloadExcel();
                }}
              >
                <CIcon name="cilDataTransferDown" className="mr-2" />
                <span>Unduh Excel</span>
              </CButton>
            </CFormGroup>
          </CCol>
        </CRow>
        <CCardGroup className="mb-12">
          <CCard className="mr-3 py-3 d-flex align-items-center justify-content-center">
            <CRow>
              <CCol className="d-flex justify-content-center">
                <CImg src="logo/medical-mask.svg" height="45" />
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>Total Pengguna</span>
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>{this.state.dashboardList.totalPengguna}</span>
              </CCol>
            </CRow>
          </CCard>
          <CCard className="mr-3 py-3 d-flex align-items-center justify-content-center">
            <CRow>
              <CCol className="d-flex justify-content-center">
                <CImg src="logo/medical-mask.svg" height="45" />
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>Pengguna Baru Hari Ini</span>
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>{this.state.dashboardList.totalPengguna}</span>
              </CCol>
            </CRow>
          </CCard>
          <CCard className="mr-3 py-3 d-flex align-items-center justify-content-center">
            <CRow>
              <CCol className="d-flex justify-content-center">
                <CImg src="logo/heart.svg" height="45" />
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>Pasien yang Memenuhi Prokes</span>
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>{this.state.dashboardList.pasienProkes}</span>
              </CCol>
            </CRow>
          </CCard>
          <CCard className="mr-3 py-3 d-flex align-items-center justify-content-center">
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <CImg src="logo/nurse.svg" height="45" />
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>Jumlah Volunteer</span>
              </CCol>
            </CRow>
            <CRow>
              <CCol className="d-flex justify-content-center text-center">
                <span>{this.state.dashboardList.jumlahVolunteer}</span>
              </CCol>
            </CRow>
          </CCard>
        </CCardGroup>
        <CRow className="my-3">
          <CCol md="2">
            <CLabel htmlFor="date-input">Filter Tanggal</CLabel>
            <CInput placeholder="Tanggal Awal" type="date" />
            {/* <DatetimeRangePicker
                timeFormat={false}
                closeOnSelect
                onChange={this.handleDateChange}
              /> */}
          </CCol>
          <span className="mt-4">
            <span className="mb-5">
              <strong>
                <p className="mt-3">
                  =
                </p>
              </strong>
            </span>
          </span>
          <CCol md="2">
            <CLabel></CLabel>
            <CInput placeholder="Tanggal Akhir" type="date" className="mt-2" />
          </CCol>
          <CCol md="2" className="mt-1">
            <CLabel></CLabel>
            <CButton
              color="outline-info"
              className="mt-4 mr-2"
              size="m"
              onClick={() => {
                this.getAllPatient(
                  localStorage.getItem("puskesmasId"),
                  ""
                );
              }}
            >
              Reset
            </CButton>
            <CButton
              color="info"
              className="mt-4"
              size="m"
              onClick={() => {
                this.filterDate();
              }}
            >
              Cari
            </CButton>
          </CCol>
          <CCol md="6">
          </CCol>
        </CRow>
        {/* {localStorage.getItem("role") != "volunteer" &&
            localStorage.getItem("role") != "operator" && (
              <CCol sm="3">
                <CFormGroup>
                  <CLabel htmlFor="search-input">Lokasi PKM</CLabel>
                  <CSelect
                    id="namaPuskesmas"
                    onChange={this.handlePuskesmasChange.bind(this)}
                    value={this.state.namaPuskesmas}
                  >
                    <option value="" key="">
                      -- Pilih Puskesmas --
                    </option>
                    {this.state.puskesmas.map((item) => {
                      return (
                        <option value={item.key} key={item.key}>
                          {item.label}
                        </option>
                      );
                    })}
                  </CSelect>
                </CFormGroup>
              </CCol>
            )} */}
        {/* {localStorage.getItem("role") != "volunteer" &&
            localStorage.getItem("role") != "operator" &&
            this.state.namaPuskesmas == "" && (
              <CCol sm="3">
                <CFormGroup>
                  <CLabel htmlFor="search-input">Jenis User</CLabel>
                  <CSelect
                    id="aktivuser"
                    onChange={this.handleAktivChange.bind(this)}
                    value={this.state.aktivuser}
                  >
                    <option value="" key="">
                      -- Pilih Aktiv User --
                    </option>
                    <option value="true" key="true">
                      Aktiv User
                    </option>
                    <option value="false" key="false">
                      Regular User
                    </option>
                  </CSelect>
                </CFormGroup>
              </CCol>
            )} */}

        {/* Card Pie Chart */}
        <CRow>
          <CCol md="4">
            <CCard>
              <CCardHeader className="text-center">
                <strong>Usia</strong>
              </CCardHeader>
              <CCardBody>
                <CChartPie
                  datasets={this.state.usiaDataSets}
                  labels={["<18", "18-35", "35-60", ">60"]}
                  options={{
                    tooltips: {
                      enabled: true,
                    },
                  }}
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol md="4">
            <CCard>
              <CCardHeader className="text-center">
                <strong>Jenis Kelamin</strong>
              </CCardHeader>
              <CCardBody>
                <CChartPie
                  datasets={this.state.genderDataSets}
                  labels={["Laki Laki", "Perempuan"]}
                  options={{
                    tooltips: {
                      enabled: true,
                    },
                  }}
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol md="4">
            {localStorage.getItem("role") != "volunteer" && (
              <CCard>
                <CCardHeader className="text-center">
                  <strong>Jenis Pengguna</strong>
                </CCardHeader>
                <CCardBody>
                  <CChartPie
                    datasets={this.state.jenisPenggunaDataSets}
                    labels={["Aktiv User", "Regular User"]}
                    options={{
                      tooltips: {
                        enabled: true,
                      },
                    }}
                  />
                </CCardBody>
              </CCard>
            )}
          </CCol>
        </CRow>
        <CRow>
          <CCol md="6">
            <CCard>
              <CCardHeader className="text-center">
                <strong>Status Monitor</strong>
              </CCardHeader>
              <CCardBody>
                <CChartPie
                  datasets={this.state.statusMonitoringDataSets}
                  labels={[
                    "Selesai dimonitor",
                    "Sedang dimonitor",
                    "Gagal dimonitor",
                    "Rujuk RS",
                    "Rujuk Shelter"
                  ]}
                  options={{
                    tooltips: {
                      enabled: true,
                    },
                  }}
                />
              </CCardBody>
            </CCard>
          </CCol>
          <CCol md="6">
            <CCard>
              <CCardHeader className="text-center">
                <strong>Diagnosa Pasien</strong>
              </CCardHeader>
              <CCardBody>
                <CChartPie
                  datasets={this.state.diagnosaDataSets}
                  labels={[
                    "Suspek",
                    "Konfirmasi",
                    "Kontak Erat",
                    "Probable",
                    "Sehat",
                  ]}
                  options={{
                    tooltips: {
                      enabled: true,
                    },
                  }}
                />
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
        {/* Grafik Penyakit Penyerta dan Gejala */}
        <CCardGroup columns className="cols-2">
          <CRow>
            <CCol>
              <CCard>
                <CCardHeader>
                  <strong>Penyakit Penyerta</strong>
                </CCardHeader>
                <CCardBody>
                  <CChartBar
                    datasets={[
                      {
                        label: "Penyakit Penyerta",
                        backgroundColor: "rgba(255,99,132,0.2)",
                        borderColor: "rgba(255,99,132,1)",
                        borderWidth: 1,
                        hoverBackgroundColor: "rgba(255,99,132,0.4)",
                        hoverBorderColor: "rgba(255,99,132,1)",
                        data: this.state.dashboardList.filterPenyakitPenyerta,
                      },
                    ]}
                    options={{
                      tooltips: {
                        enabled: false,
                      },
                    }}
                    labels={[
                      "Tidak Ada",
                      "Hamil",
                      "DM",

                      "Jantung",
                      "HT",
                      "keganasan",
                      "Autoimun",
                      "Ginjal Kronis",
                      "Hati Kronis",

                      "Asma",
                      "PPOK",
                      // "Lainnya",
                    ]}
                  />
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
          <CRow>
            <CCol>
              <CCard>
                <CCardHeader>
                  <strong>Gejala</strong>
                </CCardHeader>
                <CCardBody>
                  <CChartBar
                    datasets={[
                      {
                        label: "Gejala",
                        backgroundColor: "rgba(255,99,132,0.2)",
                        borderColor: "rgba(255,99,132,1)",
                        borderWidth: 1,
                        hoverBackgroundColor: "rgba(255,99,132,0.4)",
                        hoverBorderColor: "rgba(255,99,132,1)",
                        data: this.state.dashboardList.filterGejalaHarian,
                      },
                    ]}
                    options={{
                      tooltips: {
                        enabled: false,
                      },
                    }}
                    labels={[
                      "Demam",
                      "Demam 14 Hari Sebelumnya",
                      "Batuk",
                      "Kelelahan",
                      "Sakit Kepala",
                      "Nyeri Otot",
                      "Nyeri Tenggorokan",
                      "Pilek",
                      "Tidak Nafsu Makan",
                      "Diare",
                      "Anosmia",
                      "Ageusia",
                      "Sesak Nafas",
                      "Nyeri Dada",
                      "Wajah Membiru",
                      "Sulit Bangun",
                      // "lainnya",
                    ]}
                  />
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </CCardGroup>
        {/* Card Lokasi Kelurahan Pasien */}
        <CCardGroup>
          <CCard>
            <CRow>
              <CCol>
                <CFormGroup>
                  <CCardHeader>
                    <strong>Lokasi Kelurahan Pasien</strong>
                  </CCardHeader>
                  <CCardBody>
                    <CDataTable
                      pagination
                      itemsPerPage={5}
                      items={this.state.lokasiPasien}
                      fields={[
                        {
                          key: "no",
                        },
                        {
                          key: "kelurahan",
                          label: "Nama Kelurahan",
                        },
                        {
                          key: "counting",
                          label: "Jumlah Pasien",
                        },
                      ]}
                      scopedSlots={{
                        no: (item, index) => <td>{index + 1}</td>,
                      }}
                    />
                  </CCardBody>
                </CFormGroup>
              </CCol>
            </CRow>
          </CCard>
        </CCardGroup>
      </>
    );
  }
}
